import { TransactionsAccountOptions } from './transactions-account-options.model';
import { TransactionsFilterOptions } from './transactions-filter-options.model';
import { TransactionsSearchOptions } from './transactions-search-options.model';
import { TransactionsSortOptions } from './transactions-sort-options.model';
import { TransactionsExportOptions } from './transactions-export-options.model';

export enum OnLoadAction {
  Replace,
  Append,
  ReplacePage,
}

export enum TransactionState {
  completed = 'COMPLETED',
  uncompleted = 'UNCOMPLETED',
}

export interface TransactionsListOptions {
  account: TransactionsAccountOptions;
  search?: TransactionsSearchOptions;
  filter?: TransactionsFilterOptions;
  state?: TransactionState;
  pagination: {
    from: number;
    size: number;
  };
  sort?: TransactionsSortOptions;
  onLoad?: OnLoadAction;
}

export const defaultAccountOptions: Pick<TransactionsListOptions, 'account'> = {
  account: {},
};

export const defaultSearchOptions: Pick<TransactionsListOptions, 'search'> = {
  search: {},
};

export const defaultFilterOptions: Pick<TransactionsListOptions, 'filter'> = {
  filter: {},
};

export const defaultSortOptions: Pick<TransactionsListOptions, 'sort'> = {};

export const defaultPaginationOptions: Pick<TransactionsListOptions, 'pagination'> = {
  pagination: {
    from: 0,
    size: 10,
  },
};

export const defaultInitialListOptions: TransactionsListOptions = {
  ...defaultAccountOptions,
  ...defaultSearchOptions,
  ...defaultFilterOptions,
  ...defaultSortOptions,
  ...defaultPaginationOptions,
  onLoad: OnLoadAction.Replace,
};

export const defaultInitialExportOptions: TransactionsExportOptions = {
  ...defaultAccountOptions,
  ...defaultSearchOptions,
  ...defaultFilterOptions,
  ...defaultSortOptions,
  exportType: 'csv',
  locale: 'en-US',
};
